  /* Location of the box */
  left: 0;
  top: 0;
  padding-bottom: 30px;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */ }
  .modal-content {
    background-color: #17181c;
